<template>
  <div class="form-input-container">
    <!-- v-modelvalueemitinput -->
    <input class="input"
           :value="value"
           v-bind="$attrs"
           @input="onInput" />
  </div>
</template>

<script>
import emitter from '@/mixins/emitter'
export default {
  inheritAttrs: false,
  mixins: [emitter],
  props: {
    value: String,
  },
  methods: {
    onInput(e) {
      this.$emit('input', e.target.value)
      // $parentform-item-inputform-item
      // $parentform-item
      // console.log('parent', this.$parent)
      // this.$parent.$emit('validate')
      // mixinsdispatchform-item
      this.dispatch('form-item', 'validate')
    },
  },
}
</script>

<style lang="scss" scoped>
.form-input-container {
  flex: auto;
  .input {
    width: 100%;
    height: 20px;
    border: 1px solid lightgrey;
    border-radius: 4px;
    padding: 5px;
  }
}
</style>
