<template>
  <div>
    <div class="form-container">
      <h3></h3>
      <div class="effect">
        <MForm ref="form"
               :model="model"
               :rules="rules"
               label-position="right"
               label-width="100px">
          <MFormItem label=""
                     prop="username">
            <MFormInput v-model="model.username"
                        placeholder=""></MFormInput>
          </MFormItem>
          <MFormItem label=""
                     prop="password">
            <MFormInput type="password"
                        v-model="model.password"
                        placeholder=""></MFormInput>
          </MFormItem>
          <MFormItem>
            <button @click="login"></button>
          </MFormItem>
        </MForm>
      </div>
      <usage></usage>
      <h3></h3>
      <rl-desc :des="desData"></rl-desc>
      <im1></im1>
      <point>async-validatorhttps://github.com/yiminghe/async-validator</point>
      <im2></im2>
    </div>
  </div>
</template>

<script>
import usage from './usage.md'
import im1 from './im.md'
import im2 from './im2.md'
import formInput from './form-input'
import formItem from './form-item'
import form from './form'

export default {
  components: {
    usage,
    im1,
    im2,
    MForm: form,
    MFormInput: formInput,
    MFormItem: formItem,
  },
  data() {
    return {
      desData: [
        {
          title: 'MForm',
          content: 'modelrules',
        },
        {
          title: 'MFormItem',
          content: 'labelerrorMFormInput',
        },
        {
          title: 'MFormInput',
          content: 'v-modelmodel',
        },
      ],
      model: {
        username: '',
        password: '',
      },
      rules: {
        username: [{ required: true, message: '' }],
        password: [{ required: true, message: '' }],
      },
    }
  },
  methods: {
    login() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$notice.success({
            title: '',
            content: '',
          })
        } else {
          this.$notice({
            title: '',
            type: 'error',
            content: '',
            duration: 3000,
          })
        }
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.form-container {
  .effect {
    width: 500px;
    padding-left: 95px;
  }
}
